.menu-main {
	height: 52px;
	font-family: $font-hypatia;

	&__list {
		display: flex;
		height: inherit;
		margin: 0 -10px;

		li {
			height: inherit;

			&:hover {
				.menu-main__link {
					text-shadow: none;
					color: #1a273e;

					&::before {
						opacity: 1;
					}
				}

				.menu-main__wrap {
					display: block;
				}
			}
		}
	}

	&__link {
		position: relative;
		display: flex;
		align-items: center;
		height: inherit;
		padding: 0 10px;
		font: normal 14px/22px $font-hypatia;
		letter-spacing: 0.84px;
		white-space: nowrap;
		text-transform: uppercase;
		text-shadow: 0 0 5px rgba(0, 16, 16, 0.29);
		color: $color-white;

		&::before {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: #eeeeef;
			border-radius: 4px;
			z-index: -1;
			opacity: 0;
			transition: opacity 0.2s;
		}
	}

	&__wrap {
		position: absolute;
		top: calc(100% - 20px);
		left: 0;
		width: 100%;
		padding: 60px 0 20px;
		display: none;
		background: #eeeeef url('../images/bg_dropdown_menu.png') no-repeat center -15% / calc(100% - 400px);

		&--pt-small {
			padding-top: 34px;
		}
	}

	&__sublist {
		display: flex;
		flex-wrap: wrap;
		padding: 0 8px;

		li {
			margin-bottom: 50px;

			@media (min-width: $device-xxl) {
				flex-basis: 25%;
			}
		}
	}

	&__sublink {
		display: flex;
		align-items: center;
		color: #64697e;
		transition: color 0.2s;

		&:hover {
			color: #1a273e;

			.menu-main__picture::after {
				opacity: 0.4;
			}
		}
	}

	&__picture {
		position: relative;
		display: flex;
		align-items: center;
		margin-right: 25px;

		&::before {
			content: '';
			position: absolute;
			top: -8px;
			left: -8px;
			width: 96px;
			height: 96px;
			border: 2px solid $color-yellow;
			border-radius: 50%;
		}

		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border-radius: 50%;
			background-color: #1a273e;
			opacity: 0;
			transition: opacity 0.2s;
		}

		img {
			width: 80px;
			height: 80px;
			border-radius: 50%;
			object-fit: cover;
			overflow: hidden;
		}
	}

	&__title {
		padding-right: 10px;
		font: 700 13px/20px $font-hypatia;
		letter-spacing: 0.65px;
		text-transform: uppercase;
	}

	&__dropdown {
		max-width: 56.25%;
		columns: 320px 2;
		column-gap: 80px;
	}

	&__dropdown-item {
			margin-bottom: 21px;
	}

	&__dropdown-head {
		font: 700 13px/20px $font-hypatia;
		letter-spacing: 0.65px;
		text-transform: uppercase;
		color: #1a273e;
	}

	&__dropdown-list {
		margin-top: 5px;
		padding-top: 15px;
		border-top: 2px solid #fae26c;;
	}

	&__dropdown-link {
		font: 400 13px/21px Arial;
		text-decoration-color: transparent;
		color: #64697e;
		transition: text-decoration-color 0.2s;

		&:hover {
			text-decoration: underline;
			text-decoration-color: #64697e;
		}
	}
}