.menu-top {
	width: 100%;
	max-width: 495px;
	margin-bottom: 0;

	&__list {
		display: flex;
		justify-content: space-between;
	}

	&__link {
    font-weight: 600;
		font-size: 11px;
		line-height: 22px;
		font-family: Arial, sans-serif;
		letter-spacing: 3.3px;
		text-transform: uppercase;
		color: $color-gray;
	}
}