*,
*::before,
*::after {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	box-sizing: border-box;
}

ul {
	list-style-type: none;
}

a {
	outline: transparent;
	text-decoration: none;
	color: $color-black;
}

button {
	outline: transparent;
	background-color: transparent;
	border: none;
}

.no-scroll {
	overflow: hidden;
}
