.header {
	position: fixed;
	width: 100%;
	z-index: 103;
	padding: 17px 0 18px;
	background-color: transparent;
	transition: background-color 0.2s;

	&--white {
		background-color: $color-white;

		.header__btn {
			color: #64697e;
		}

		.header__burger {
			color: #1a273e;
		}

		.menu-main__link {
			color: #1a273e;
			text-shadow: none;
		}
	}

	&__container {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;

    @media (min-width: $device-xxl) {
      justify-content: flex-start;
    }
  }

	&__logo {
		display: inline-block;
		width: 100%;
		max-width: 153px;

		img {
			display: block;
			width: 100%;
		}
	}

	&__menu {
		display: none;

		@media (min-width: $device-xxl) {
			display: block;
      padding-top: 8px;
      padding-left: 57px;
      padding-right: 30px;
		}
	}

	&__action {
		display: none;
		padding-top: 5px;

		@media (min-width: $device-md) {
			display: block;
		}
	}

	&__wrap {
		display: flex;
		align-items: center;
	}

	&__phone {
		font: 700 20px/22px $font-hypatia;
		text-transform: uppercase;
		color: #1a273e;
	}

	&__separator {
		width: 1px;
		height: 24px;
		margin-left: 16px;
		margin-right: 17px;
		background-color: #445569;
		opacity: 0.5;
	}

	&__search {
		display: flex;
		width: 12px;
		height: 12px;

		svg {
			width: 100%;
			height: 100%;
			fill: #445569;
			fill-opacity: 0.5;
		}
	}

	&__btn {
		height: 27px;
		margin-bottom: 10px;
		padding: 0 29px;
		border: 2px solid rgba($color-yellow, 0.5);
		border-radius: 17px;
		font: 400 9px Arial;
		letter-spacing: 1.8px;
		text-transform: uppercase;
		color: $color-white;

		span {
			display: flex;
			align-items: center;
		}
	}

	&__icon {
		width: 12px;
		height: 12px;
		margin-right: 6px;

		svg {
			width: 100%;
			height: 100%;
			fill: $color-yellow;
		}
	}

	&__burger {
		align-self: center;
		font: 700 20px $font-hypatia;
		text-transform: uppercase;
		color: $color-white;

		&--black {
			color: #1a273e;
		}

		@media (min-width: $device-xxl) {
			display: none;
		}
	}
}
