.sidebar {
	position: fixed;
	top: 385px;
	right: 0;
	z-index: 99;

	&__item {
		display: block;
		width: 50px;
		height: 50px;
		margin-bottom: 2px;
		border-top-left-radius: 2px;
		border-bottom-left-radius: 2px;
		background-color: $color-yellow;

		span {
			display: flex;
			align-items: center;
			justify-content: center;
		}
	}

	&__icon {
		width: 30px;
		height: 30px;

		svg {
			width: 100%;
			height: 100%;
		}
	}
}
