.banners {
	padding: 4px;
	padding-bottom: 0;
	background-color: $color-white;

	@media (min-width: $device-md) {
    display: flex;
	}

  @media (min-width: $device-xl) {
    width: calc(100% - 31.25%);
  }

  &__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
    align-content: space-between;
    width: 100%;

		@media (min-width: $device-md) {
			padding-left: 4px;
		}
	}

	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		flex-basis: calc(50% - 2.5px);
		height: 156px;
		overflow: hidden;

		@media (min-width: $device-md) {
			flex-basis: calc(33.3333% - 2.5px);
		}

		&--big {
			flex-basis: 314px;
			height: 318px;

			@media (min-width: $device-xxl) {
				flex-shrink: 0;
			}
		}

    &:hover {
      .banners__img {
        transform: scale(1.1);
      }
    }
  }

  &__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
  }

  &__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
    object-fit: cover;
    transform: scale(1);
    transition: transform 0.2s;
	}

	&__title {
		padding-top: 17px;
		padding-left: 17px;
		z-index: 1;
		font: 700 15px/22px $font-hypatia;
		letter-spacing: 0.75px;
		text-transform: uppercase;
		text-shadow: 0 0 5px rgba(28, 47, 80, 0.6);
		color: $color-white;
	}

	&__arrow {
		position: absolute;
		bottom: 20px;
		left: 17px;
		width: 23px;
		height: 23px;

		svg {
			width: 100%;
			height: 100%;
		}
	}
}
