.button {
	display: inline-flex;
	align-items: center;
	width: auto;
	height: 35px;
	font: 700 10px/22px Arial;
	letter-spacing: 1px;
	text-transform: uppercase;
	color: #1a273e;


	&--link {
		padding: 0 50px;
		border: 4px solid $color-white;
		border-radius: 17px;
		background: $color-yellow url('../images/bg-btn.png') no-repeat center / cover;
	}
}
