.footer {
	padding: 30px 0;
	background: #efefef url('../images/bg_footer.png') no-repeat bottom center / calc(100% - 200px);

	&__wrap {
		@media (min-width: $device-lg) {
			display: flex;
			justify-content: space-between;
		}
	}

	&__map-site {
		margin-bottom: 30px;

		@media (min-width: $device-lg) {
			flex-basis: 68.75%;
			margin-bottom: 0;
		}
	}

	&__contact {
		text-align: center;

		@media (min-width: $device-lg) {
			text-align: right;
		}
	}

	&__tel {
		display: block;
		margin-bottom: 6px;
		font: 700 32px Arial;
		color: #1a273e;

		@media (min-width: $device-md) {
			font: 700 40px Arial;
		}
	}

	&__btn {
		display: inline-block;
		font: 400 9px/25px Arial;
		letter-spacing: 2.7px;
		text-transform: uppercase;
		color: #64697e;

		&:not(:first-of-type) {
			margin-left: 8px;
		}

		span {
			display: flex;
			align-items: baseline;
		}
	}

	&__icon {
		display: flex;
		width: 12px;
		height: 12px;
		margin-right: 10px;

		&--middle {
			width: 13px;
			height: 13px;
		}

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&__authorization {
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 16px 0 28px;
		padding-right: 17px;
		padding-left: 24px;
		border: 2px solid $color-white;

		@media (min-width: $device-lg) {
			justify-content: flex-start;
		}
	}

	&__btn-authorization {
		height: 37px;
		font: 400 9px Arial;
		letter-spacing: 2.7px;
		text-transform: uppercase;
		color: #64697e;

		&:first-of-type {
			order: 1;
		}

		&:last-of-type {
			order: 3;
		}

		span {
			height: inherit;
			display: flex;
			align-items: center;
		}
	}

	&__separator {
		order: 2;
		width: 1px;
		height: 19px;
		margin: 0 8px;
		background-color: #1a273e;
		opacity: 0.1;

		@media (min-width: $device-md) {
			margin: 0 16px;
		}
	}
}
