.main-slider {
	position: relative;
	height: 799px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;

	&__img {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
		height: inherit;
		z-index: -1;
	}

	&__slide {
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}

	&__content {
		display: flex;
		align-items: center;
		height: inherit;
	}

	&__container {
		height: auto;
	}

	&__wrap {
		width: 100%;
		max-width: 367px;
		font-family: $font-hypatia;
		color: #1a273e;
	}

	&__title {
		margin-bottom: 10px;
		font-weight: 700;
		font-size: 45px;
		line-height: 45px;
		color: $color-white;
		text-shadow: 0 0 5px rgba(0, 16, 16, 0.5);
		text-transform: uppercase;
	}

	&__offer {
		margin-bottom: 15px;
		font-weight: 400;
		font-size: 28px;
		line-height: 40px;
		text-transform: uppercase;

		span {
			display: inline-block;
			padding: 0 10px;
			background-color: $color-yellow;

			&:not(:last-of-type) {
				margin-bottom: 2px;
			}
		}
	}

	&__text {
		margin-bottom: 15px;
		font: 400 12px/18px Arial;

		span {
			padding: 0 4px;
			background-color: $color-white;
		}
	}

	&__pagination {
		top: 75%;
		bottom: auto !important;
    transform: translateY(-75%);
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 50px;

		.swiper-pagination-bullet {
			width: 7px;
			height: 7px;
			border-radius: 0;
			background-color: #161a12;
			opacity: 1;
		}

		.swiper-pagination-bullet-active {
			width: 10px;
			height: 10px;
			background-color: $color-yellow;
		}
	}
}