.popup-modal {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: flex;
	align-items: center;
	width: 100%;
	height: 100%;
	padding: 60px 15px;
	background-color: $color-white;

	@media (min-width: $device-md) {
		display: block;
		max-width: 40%;
		height: auto;
		padding: 60px 30px;
	}

	&__close {
		position: absolute;
		top: 20px;
		right: 20px;
		width: 16px;
		height: 16px;
		cursor: pointer;

		svg {
			width: 100%;
			height: 100%;
		}
	}
}
