.news {
	position: relative;
	margin-bottom: 20px;
	padding: 20px 12px 25px;
	border: 3px solid $color-yellow;

	&__slider {
		@media (min-width: $device-md) {
			max-width: 90%;
		}

		@media (min-width: $device-xxl) {
			max-width: 1180px;
		}
	}

	&__arrow {
		position: absolute;
		top: 50%;
		display: none;
		width: 7px;
		height: 12px;
		z-index: 10;
		outline: transparent;
		cursor: pointer;

		@media (min-width: $device-md) {
			display: block;
		}

		&--prev {
			left: 15px;
		}

		&--next {
			right: 15px;

			svg {
				transform: rotate(180deg);
			}
		}

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&__item {
		display: flex;
		align-items: flex-start;
	}

	&__img {
		flex-shrink: 0;
		width: 74px;
		height: 74px;
		border-radius: 2px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}

	&__title {
		margin-bottom: 20px;
		font: 600 16px/20px Arial;
		letter-spacing: 1.6px;
		text-align: center;
		text-transform: uppercase;
		color: #64697e;
	}

	&__content {
		padding-left: 20px;
	}

	&__subtitle {
		font: 700 11px/20px $font-hypatia;
		letter-spacing: 1.1px;
		text-transform: uppercase;
		color: #1a273e;
	}

	&__icon {
		width: 10px;
		height: 10px;
		margin-right: 7px;

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&__date {
		display: flex;
		align-items: baseline;
		font: italic 400 11px/20px Arial;
		letter-spacing: 0.55px;
		color: #a4a8b6;
	}

	&__pagination {
		position: static;
		top: auto;
		left: auto;
		bottom: auto;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 20px auto 0;

		.swiper-pagination-bullet {
			width: 4px;
			height: 4px;
			border-radius: 0;
			background-color: #a4a8b6;
			opacity: 1;
			transform: scale(1);
		}

		.swiper-pagination-bullet-active {
			width: 8px;
			height: 8px;
			background-color: #64697e;
		}
	}
}
