.menu-mobile {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 102;
	padding-top: 130px;
	background-color: $color-white;
	overflow: scroll;

	&__title {
		margin-bottom: 15px;
		font: 700 24px $font-hypatia;
		text-transform: uppercase;
		color: #1a273e;

		@media (min-width: $device-md) {
			font-size: 32px;
		}

		@media (min-width: $device-lg) {
			font-size: 45px;
			line-height: 45px;
		}

		span {
			position: relative;
			display: inline-block;

			&::after {
				content: '';
        position: absolute;
        top: 50%;
        right: -25px;
        width: 10px;
        height: 10px;
        border-color: #1a273e;
        border-top: none;
        border-right: 2px solid;
        border-bottom: 2px solid;
        transform: translateY(-100%) rotate(45deg);
			}
		}

    .menu-mobile__list {
      margin-top: 15px;
    }
  }

	&__link {
		display: block;
		margin-bottom: 15px;
		font: 700 24px $font-hypatia;
		text-transform: uppercase;
		color: #1a273e;

		@media (min-width: $device-md) {
			font-size: 32px;
		}

		@media (min-width: $device-lg) {
			font-size: 45px;
			margin-bottom: 15px;
		}
	}
}
