.map-site {
	columns: 140px 3;
	column-gap: 40px;

	@media (min-width: $device-xxl) {
		columns: 240px 3;
		column-gap: 80px;
	}

	&__title {
		display: block;
		margin-bottom: 17px;
		font: 700 11px/25px $font-hypatia;
		letter-spacing: 0.55px;
		text-transform: uppercase;
		color: #64697e;

		@media (min-width: $device-md) {
			margin-bottom: 0;
		}

		&--mt-big {
			@media (min-width: $device-md) {
				margin-top: 60px;
			}
		}
	}

	&__list {
		margin-bottom: 17px;
	}

	&__link {
		display: inline-block;
		font: 400 11px/25px Arial;
		letter-spacing: 0.55px;
		text-decoration: underline;
		text-decoration-color: transparent;
		color: #a4a8b6;
		transition: text-decoration-color 0.2s;

		&:hover {
			text-decoration-color: #a4a8b6;
		}
	}
}