.content {
	position: relative;
	display: flex;
	padding-bottom: 30px;
	background-color: $color-white;

	&::before {
		content: '';
		position: absolute;
		top: -20;
		width: 100%;
		height: 42px;
		background: url('../images/waves.png') repeat-x left / contain;
		z-index: -1;
	}

	&__container {
		margin-top: -165px;
	}

	&__row {
		margin-bottom: 20px;

		@media (min-width: $device-xl) {
			display: flex;
		}
	}
}