.benefits {
	flex-shrink: 0;
	flex-basis: 31.25%;
	background: $color-yellow url('../images/benefits_bg.png') no-repeat top right / contain;

	&__item {
		position: relative;
		display: flex;
		align-items: center;
		height: 33.3333%;
		padding: 20px;

		&::after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 1px;
			background-color: #e8c61f;
		}

		@media (min-width: $device-xl) {
			padding: 0 20px;
		}
	}

	&__icon {
		flex-shrink: 0;
		width: 60px;
		height: 60px;

		svg {
			width: 100%;
			height: 100%;
			fill: $color-white;
		}
	}

	&__content {
		padding-left: 20px;
	}

	&__title {
		margin-bottom: 5px;
		font: 700 15px/20px $font-hypatia;
		letter-spacing: 0.75px;
		text-transform: uppercase;
		color: #4c3f00;
	}

	&__text {
    font: 400 12px/18px Arial;
		color: #8b7509;

    @media (min-width: $device-xl) {
      max-width: 250px;
    }
  }
}
