.seo-block {
	position: relative;
	display: flex;
	min-height: 370px;
	padding: 35px 15px;
	padding-right: 49px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;

	@media (min-width: $device-md) {
		padding: 45px;
	}

	&:not(:last-of-type) {
		margin-bottom: 20px;
	}

	&::after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 4px;
		background-color: $color-yellow;
		z-index: 9;
	}

	&__content {
		max-width: 592px;

		&--right {
			margin-right: 0;
			margin-left: auto;
		}
	}

	&__title {
		margin-bottom: 17px;
		font: 700 20px/25px $font-hypatia;
		text-transform: uppercase;
		color: #1a273e;
	}

	&__text {
		font: 400 12px/18px Arial;
		color: #64697e;
	}
}
